* {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav {
  text-transform: uppercase;
}

.nav__li {
  margin-left: 20px;
}

.nav__li:first-child {
  margin-left: 0;
}

.nav__li:last-child {
  margin-right: 0;
}

.nav__list {
  text-align: center;
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s ease-in-out;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  flex-shrink: 0;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.logo {
  width: 40px;
  height: 40px;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 16px;
  line-height: 14px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.icon {
  width: 160px;
  height: 134px;
  margin: 0 auto 23px;
  padding-top: 16px;
  display: block;
}

.reviews {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.reviews__number {
  color: #060b35;
  text-align: center;
  padding-top: 2px;
  padding-left: 17px;
  font-weight: 400;
}

.btn {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.catalog {
  grid-template-columns: 1fr, 200px;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  padding-top: 17px;
  font-size: 12px;
  line-height: 18px;
}

.card:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  height: 18px;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 500;
}

body {
  margin: 0;
}
